// =============================================================================
// VERCEL CRON ENDPOINT: x402 Ecosystem Discovery
// =============================================================================
// GET /api/cron/x402-discovery
// Crawls x402 ecosystem domains from the watchlist and tracks changes
// Called by Vercel Cron (schedule configurable in vercel.json)
//
// Query params:
// - maxPriority: Only crawl items with priority >= this value (1-5)
// - method: HEAD or GET (default: HEAD) - Note: discovery endpoints always use GET
// - timeout: Request timeout in ms (default: 10000)
//
// Categories:
//   - bazaar_discovery, facilitator_discovery: Machine-callable APIs (GET, parse JSON, store hash)
//   - spec, index, gateway_docs, facilitator_docs, etc.: Docs/HTML (HEAD/GET, store ETag/hash)

import { NextRequest } from 'next/server';
import { createHash } from 'crypto';
import { createLogger } from '@/lib/logger';
import { withCronJobTracking, type CronJobContext } from '@/lib/cron-job-wrapper';
import {
  getActiveWatchlistItems,
  updateWatchlistItemAfterCheck,
  type X402WatchItem,
} from '@/db/x402-watchlist';
import { notifyX402DiscoveryCrawl } from '@/lib/slack-notifier';

const logger = createLogger({ component: 'X402DiscoveryCron' });

interface CrawlResult {
  id: string;
  root_domain: string;
  path_prefix: string | null;
  category: string;
  priority: number;
  url: string;
  httpStatus: number;
  etag: string | null;
  changed: boolean;
  error?: string;
}

/**
 * Check if a category is a machine-callable discovery endpoint
 */
function isDiscoveryEndpoint(category: string): boolean {
  return category === 'bazaar_discovery' || category === 'facilitator_discovery';
}

/**
 * Generate a content hash from response body (first 64KB)
 */
function generateContentHash(content: string): string {
  return createHash('sha256').update(content.slice(0, 65536)).digest('hex').slice(0, 32);
}

/**
 * Crawl a single watchlist item
 * For discovery endpoints: Uses GET, expects JSON, stores hash
 * For docs: Uses HEAD/GET, stores ETag or content hash
 */
async function crawlItem(
  item: X402WatchItem,
  defaultMethod: 'HEAD' | 'GET',
  timeoutMs: number
): Promise<CrawlResult> {
  const url = `https://${item.root_domain}${item.path_prefix || ''}`;
  const result: CrawlResult = {
    id: item.id,
    root_domain: item.root_domain,
    path_prefix: item.path_prefix,
    category: item.category,
    priority: item.priority,
    url,
    httpStatus: 0,
    etag: null,
    changed: false,
  };

  // Discovery endpoints always use GET and expect JSON
  const isDiscovery = isDiscoveryEndpoint(item.category);
  const method = isDiscovery ? 'GET' : defaultMethod;

  try {
    const controller = new AbortController();
    const timeoutId = setTimeout(() => controller.abort(), timeoutMs);

    const response = await fetch(url, {
      method,
      headers: {
        'User-Agent': 'NexflowSMF/1.0 x402-discovery',
        'Accept': isDiscovery ? 'application/json' : 'text/html,application/json,*/*',
      },
      signal: controller.signal,
      redirect: 'follow',
    });

    clearTimeout(timeoutId);

    result.httpStatus = response.status;

    if (isDiscovery) {
      // Machine-callable discovery endpoints: parse JSON and hash it
      if (response.ok) {
        try {
          const jsonText = await response.text();
          let jsonData: any;
          try {
            jsonData = JSON.parse(jsonText);
          } catch (parseError) {
            result.error = 'Invalid JSON response';
            logger.warn({
              url,
              category: item.category,
              status: response.status,
            }, 'Discovery endpoint returned invalid JSON');
            return result;
          }
          
          const normalizedJson = JSON.stringify(jsonData);
          result.etag = `json-hash:${generateContentHash(normalizedJson)}`;
        } catch (error) {
          result.error = error instanceof Error ? error.message : 'Failed to process JSON';
          logger.warn({
            url,
            category: item.category,
            error: result.error,
          }, 'Failed to process discovery endpoint response');
        }
      } else {
        result.error = `HTTP ${response.status}`;
        logger.warn({
          url,
          category: item.category,
          status: response.status,
        }, 'Discovery endpoint returned non-200 status');
      }
    } else {
      // Documentation pages: use ETag header or content hash
      const serverEtag = response.headers.get('etag');
      if (serverEtag) {
        result.etag = serverEtag;
      } else if (method === 'GET') {
        const text = await response.text();
        result.etag = `hash:${generateContentHash(text)}`;
      }
    }

    // Determine if content changed
    if (item.last_etag && result.etag) {
      result.changed = item.last_etag !== result.etag;
    } else if (item.last_http_status !== null && item.last_http_status !== result.httpStatus) {
      result.changed = true;
    } else if (!item.last_checked_at) {
      result.changed = true;
    }

  } catch (error) {
    if (error instanceof Error) {
      if (error.name === 'AbortError') {
        result.error = 'timeout';
        result.httpStatus = 408;
      } else {
        result.error = error.message;
        result.httpStatus = 0;
      }
    } else {
      result.error = 'Unknown error';
      result.httpStatus = 0;
    }
    
    if (isDiscoveryEndpoint(item.category)) {
      logger.warn({
        url,
        category: item.category,
        error: result.error,
      }, 'Discovery endpoint crawl failed (non-fatal)');
    }
  }

  return result;
}

// =============================================================================
// CRON HANDLER
// =============================================================================

async function x402DiscoveryHandler(
  request: NextRequest,
  context: CronJobContext
): Promise<{ ok: boolean; [key: string]: any }> {
  const { runId, requestId } = context;

  const { searchParams } = new URL(request.url);
  const maxPriority = searchParams.get('maxPriority');
  const method = (searchParams.get('method')?.toUpperCase() || 'HEAD') as 'HEAD' | 'GET';
  const timeout = parseInt(searchParams.get('timeout') || '10000', 10);

  const minPriority = maxPriority ? parseInt(maxPriority, 10) : undefined;

  logger.info({
    runId,
    requestId,
    minPriority,
    method,
    timeout,
    msg: 'Starting x402 discovery crawl',
  });

  // Get active watchlist items
  const items = await getActiveWatchlistItems(minPriority);

  if (items.length === 0) {
    logger.info({ runId, requestId, msg: 'No active watchlist items to crawl' });
    return {
      ok: true,
      checked: 0,
      changed: 0,
      errors: 0,
      message: 'No active watchlist items',
      timestamp: new Date().toISOString(),
    };
  }

  logger.info({ runId, requestId, count: items.length }, `Found ${items.length} watchlist items to crawl`);

  const results: CrawlResult[] = [];
  let changedCount = 0;
  let errorCount = 0;
  
  // Track counts by category
  const categoryCounts: Record<string, number> = {};
  const categoryChanged: Record<string, number> = {};
  const categoryErrors: Record<string, number> = {};

  // Crawl each item
  for (const item of items) {
    const result = await crawlItem(item, method, timeout);
    results.push(result);

    // Track category stats
    categoryCounts[result.category] = (categoryCounts[result.category] || 0) + 1;
    if (result.changed) {
      categoryChanged[result.category] = (categoryChanged[result.category] || 0) + 1;
    }
    if (result.error) {
      categoryErrors[result.category] = (categoryErrors[result.category] || 0) + 1;
      errorCount++;
    }

    // Log the check
    logger.info({
      runId,
      requestId,
      kind: 'x402_discovery_check',
      root_domain: result.root_domain,
      path_prefix: result.path_prefix,
      category: result.category,
      priority: result.priority,
      httpStatus: result.httpStatus,
      changed: result.changed,
      error: result.error,
      isDiscovery: isDiscoveryEndpoint(result.category),
    }, `Checked ${result.url}: status=${result.httpStatus}, changed=${result.changed}`);

    // Update database
    try {
      await updateWatchlistItemAfterCheck(
        item.id,
        result.httpStatus,
        result.etag,
        result.changed
      );

      if (result.changed && !result.error) {
        changedCount++;
      }
    } catch (dbError) {
      logger.error({
        runId,
        requestId,
        error: dbError,
        itemId: item.id,
      }, 'Failed to update watchlist item');
      errorCount++;
    }
  }

  const summary = {
    ok: errorCount === 0,
    checked: results.length,
    changed: changedCount,
    errors: errorCount,
    byCategory: {
      crawled: categoryCounts,
      changed: categoryChanged,
      errors: categoryErrors,
    },
    timestamp: new Date().toISOString(),
    results: results.map(r => ({
      domain: r.root_domain,
      path: r.path_prefix,
      category: r.category,
      priority: r.priority,
      status: r.httpStatus,
      changed: r.changed,
      error: r.error,
    })),
  };

  logger.info({
    runId,
    requestId,
    checked: summary.checked,
    changed: summary.changed,
    errors: summary.errors,
    crawled: categoryCounts,
    msg: 'x402 watchlist crawl completed',
  });

  // Send Slack notification if there are changes or errors
  await notifyX402DiscoveryCrawl(summary).catch(err => {
    logger.error({ runId, requestId, error: err }, 'Failed to send Slack notification');
  });

  return summary;
}

// =============================================================================
// EXPORTS
// =============================================================================

export const dynamic = 'force-dynamic';
export const maxDuration = 60;

export const GET = withCronJobTracking(x402DiscoveryHandler, {
  jobId: 'x402-discovery',
  timeout: 55000, // 55s (leave buffer for wrapper overhead)
  minInterval: 23 * 60 * 60 * 1000, // 23 hours (schedule is once a day)
});
