// =============================================================================
// SLACK NOTIFIER
// =============================================================================
// Simple Slack webhook integration for notifications

import { createLogger } from './logger';

const logger = createLogger({ component: 'SlackNotifier' });

export interface SlackMessage {
  text?: string;
  blocks?: SlackBlock[];
  attachments?: SlackAttachment[];
}

export interface SlackBlock {
  type: 'section' | 'header' | 'divider' | 'context';
  text?: {
    type: 'plain_text' | 'mrkdwn';
    text: string;
    emoji?: boolean;
  };
  fields?: Array<{
    type: 'plain_text' | 'mrkdwn';
    text: string;
  }>;
}

export interface SlackAttachment {
  color?: string;
  title?: string;
  text?: string;
  fields?: Array<{
    title: string;
    value: string;
    short?: boolean;
  }>;
  footer?: string;
  ts?: number;
}

/**
 * Send a message to Slack via webhook
 */
export async function sendSlackMessage(message: SlackMessage): Promise<boolean> {
  const webhookUrl = process.env.SLACK_WEBHOOK_URL;

  if (!webhookUrl) {
    logger.warn('SLACK_WEBHOOK_URL not configured, skipping notification');
    return false;
  }

  try {
    const response = await fetch(webhookUrl, {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(message),
    });

    if (!response.ok) {
      logger.error({ status: response.status }, 'Failed to send Slack message');
      return false;
    }

    logger.info('Slack message sent successfully');
    return true;
  } catch (error) {
    logger.error({ error }, 'Error sending Slack message');
    return false;
  }
}

/**
 * Send a simple text notification
 */
export async function notifySlack(text: string): Promise<boolean> {
  return sendSlackMessage({ text });
}

/**
 * Send an x402 discovery crawl summary to Slack
 */
export async function notifyX402DiscoveryCrawl(summary: {
  checked: number;
  changed: number;
  errors: number;
  timestamp: string;
  results: Array<{
    domain: string;
    path: string | null;
    category: string;
    priority: number;
    status: number;
    changed: boolean;
    error?: string;
  }>;
}): Promise<boolean> {
  // Only notify if there are changes or errors
  if (summary.changed === 0 && summary.errors === 0) {
    logger.info('No changes or errors, skipping Slack notification');
    return false;
  }

  const emoji = summary.errors > 0 ? 'ðŸš¨' : 'ðŸ“¢';
  const color = summary.errors > 0 ? '#dc3545' : '#28a745';

  const changedDomains = summary.results
    .filter(r => r.changed && !r.error)
    .map(r => `â€¢ ${r.domain}${r.path || ''} (${r.category})`)
    .join('\n');

  const errorDomains = summary.results
    .filter(r => r.error)
    .map(r => `â€¢ ${r.domain}${r.path || ''}: ${r.error}`)
    .join('\n');

  const blocks: SlackBlock[] = [
    {
      type: 'header',
      text: {
        type: 'plain_text',
        text: `${emoji} x402 Ecosystem Discovery Update`,
        emoji: true,
      },
    },
    {
      type: 'section',
      fields: [
        { type: 'mrkdwn', text: `*Checked:* ${summary.checked}` },
        { type: 'mrkdwn', text: `*Changed:* ${summary.changed}` },
        { type: 'mrkdwn', text: `*Errors:* ${summary.errors}` },
        { type: 'mrkdwn', text: `*Time:* ${new Date(summary.timestamp).toLocaleString()}` },
      ],
    },
  ];

  if (changedDomains) {
    blocks.push({
      type: 'section',
      text: {
        type: 'mrkdwn',
        text: `*ðŸ”„ Changed Domains:*\n${changedDomains}`,
      },
    });
  }

  if (errorDomains) {
    blocks.push({
      type: 'section',
      text: {
        type: 'mrkdwn',
        text: `*âŒ Errors:*\n${errorDomains}`,
      },
    });
  }

  return sendSlackMessage({
    text: `x402 Discovery: ${summary.changed} changes, ${summary.errors} errors`,
    blocks,
    attachments: [
      {
        color,
        footer: 'NexFlow SMF â€¢ x402 Discovery Crawler',
        ts: Math.floor(Date.now() / 1000),
      },
    ],
  });
}

